.App {
  text-align: center;
}

html{
  max-width: 1650px;
  margin-left: auto;
  margin-right: auto;
  background-color: #282c34;
}


.wrapper{
  width: 95%;
  margin-left: auto;
  margin-right: auto;
}

/*header*/

.header{
  width: 100%;
  min-height: 100vh;
  display: flex;
  margin-left: auto;
  margin-right: auto;
  background-image: url(./assets/images/zvezdnoe_nebo_nebo.jpg);
}

.navbar{
  width: 100%;
  background-image: url(./assets/images/zvezdnoe_nebo_nebo.jpg);
  position: absolute;
  z-index: 1;
}

.header-menu{
  display: flex;

}

.header-menu ul{
  display: flex;
  justify-content: space-between;
  width: 30%;
  margin-left: auto;
}

.header-menu ul li{
  list-style-type: none;
}

.header-about img{
  width: 5vw;
}

.header-start img{
  width: 5.8vw;
}

.header-roadmap img{
  width: 6.6vw;
}

.header-home img{
  width: 5.3vw;
}

.header-logo{
  padding-top: 1vw;
  width: 5vw;
} 

.header-conteiner{
  position: relative;
  display: flex;
  flex-direction: column;
}

.header-conteiner-title-background{
  margin-left: auto;
  margin-right: auto;
}

.header-conteiner-title-background img{
  position: absolute;
  top: 7vw;
  left: 28vw;
  width: 38vw;
  opacity: 0.2;
  z-index: 0;
}

.header-conteiner-title{
  margin-left: auto;
  margin-right: auto;
  padding-top: 6vw;
}

.header-conteiner-title img{
  position: relative;
  width: 28vw;
  padding-top: 3vw;
  padding-left: 1vw;
  z-index: 1;
}

.header-conteiner-content{
  margin-left: auto;
  margin-right: auto;
  position: relative;
}


.header-conteiner-content img{
  position: absolute;
  width: 50vw;
  top: -5vw;
  left: -25vw;
}

/*whatischiz*/

.whatischiz{
  width: 100%;
  min-height: 100vh;
  display: flex;
  margin-left: auto;
  margin-right: auto;
  background-image: url(./assets/images/zvezdnoe_nebo_nebo.jpg);
}

.whatischiz-conteiner{
  display: flex;

}

.whatischiz-conteiner{
  display: flex;
  
}

.whatischiz-conteiner-content {
  padding-top: 11vw;
  padding-left: 5vw;
}

.whatischiz-conteiner-content p{
  color: #fff;
}

.whatischiz-conteiner-content-title img{
  width: 21vw;
}

.whatischiz-conteiner-content-ca img{
  width: 26vw;
}

.whatischiz-conteiner-img{
  position: relative;
}

.whatischiz-conteiner-img-background img{
  width: 42vw;
  position: absolute;
  opacity: 0.2;
  z-index: 0;
  top: 4vw;
  left: 1vw;
}

.whatischiz-conteiner-img-main img{
  width: 40vw;
  position: absolute;
  z-index: 1;
  top: 8vw;
  left: -2vw;
}

/*start*/

.start{
  width: 100%;
  min-height: 100vh;
  display: flex;
  margin-left: auto;
  margin-right: auto;
  background-image: url(./assets/images/zvezdnoe_nebo_nebo.jpg);
}

.start-conteiner{
  display: flex;
  margin-left: auto;
  margin-right: auto;
  flex-direction: column;
}

.start-conteiner-content-title, .start-conteiner-content-background{
  padding-top: 3.5vw;
  position: relative;
  width: 50%;
  display: flex;
  margin-left: auto;
  margin-right: auto;
}

.start-conteiner-content-title img{
  width: 14vw;
  margin-left: auto;
  margin-right: auto;
}

.start-conteiner-content-background img{
  width: 24vw;
  position: absolute;
  opacity: 0.1;
  z-index: 0;
  top: 5vw;
  right: 12vw;
}

.start-conteiner-img{
  position: relative;
}

.start-conteiner-img img{
  width: 31vw;
  position: absolute;
  top: 0.5vw;
}

tr{
  background-color: blue !important;
}

/*roadmap*/

.roadmap{
  width: 100%;
  min-height: 100vh;
  display: flex;
  margin-left: auto;
  margin-right: auto;
  background-image: url(./assets/images/zvezdnoe_nebo_nebo.jpg);
}

.roadmap-conteiner{
  padding-top: 0vw;
  display: flex;
  margin-left: auto;
  margin-right: auto;
  flex-direction: column;
}

.roadmap-conteiner-content-title, .roadmap-conteiner-content-background{
  padding-top: 3vw;
  position: relative;
  width: 50%;
  display: flex;
  margin-left: auto;
  margin-right: auto;
}

.roadmap-conteiner-content-title img{
  width: 14vw;
  margin-left: auto;
  margin-right: auto;
}

.roadmap-conteiner-content-background img{
  width: 24vw;
  position: absolute;
  opacity: 0.1;
  z-index: 0;
  top: 4.5vw;
  right: 12vw;
}

.roadmap-conteiner-img{
  position: relative;
  margin-left: auto;
  margin-right: auto;
}

.roadmap-conteiner-img img{
  position: absolute;
  width: 43vw;
  top: -34.5vw;
  right: -18vw;
  opacity: 0.3;
  z-index: 0;
}

.roadmap-conteiner-content-text{
  width: 60%;
  display: flex;
  color: #fff;
  margin-left: auto;
  margin-right: auto;
  justify-content: space-between;
}

.phasone, .phastwo, .phasthree{
  border-left: 0.1vw solid #fff;
  padding-left: 1vw;
}

.phastwo{
  margin-top: 10vw;
}

.phasthree{
  margin-top: 20vw;
}

.phasone p, .phastwo p, .phasthree p{
  position: relative;
  font-size: 0.8vw;
  z-index: 1;
}

.phasone h2, .phastwo h2, .phasthree h2{
  position: relative;
  font-size: 0.8vw;
  padding-top: 6vw;
  z-index: 1;
}

.start-conteiner-nested-list {
  position: absolute;
  left: 38%;
  top: 18vw;
  width: 20vw;
  background-color: black;
  padding: 2vw;
  color: #ffd700;
  font-family: 'Comic Sans MS', cursive, sans-serif;
  z-index: 1;
}

.section {
  margin: 0.5vw 0;
  border-radius: 10px;
  overflow: hidden;
  text-align: center;
}

.list {
  background-color: #2c5fa9;
  padding: 0.1vw;
  display: flex;
  justify-content: center;
  cursor: pointer;
  position: relative;
}

.title{
  height: 2vw;
}

.plus{
  position: absolute;
  z-index: 2;
  right: 1vw;
  top: 0.3vw;
}

.content {
  background-color: #182e4c;
  color: #ffd700;
  padding: 1vw 1vw 0vw 1vw;
  display: flex;

}

.content img:nth-child(1) {
  height: 5vw;
}

.content img:nth-child(2) {
  height: 3vw;
  padding-top: 1vw;
  padding-left: 1vw;
}